li {
  list-style-type: none;
  text-align: center; }

.effect-button:hover {
  background: #345b24; }

.effect-button:active {
  background: rgba(7, 66, 7, 0.5); }

button {
  width: 150px;
  height: 50px; }

.selected {
  background-color: #7deb8b;
  border: 2px solid #48af48; }



